import React from 'react';
import Button from '@material-ui/core/Button';
const myStyle ={
    marginTop :"2rem",
    textAlign:"center"
}
export const SubmitButton =(props)=> {
    return(
        <React.Fragment>
        <div style={myStyle}>
        <Button onClick={props.clickBtn} size="small" variant="contained" color="secondary">Create Short Url</Button>
        </div>
        </React.Fragment>
    );
} 