import React from 'react';
import {Header } from '../components/header-component';
import {Input} from '../components/input-component';
import Container from '@material-ui/core/Container';
import {Instance } from '../utils/axiosInstance';
import {SubmitButton} from '../components/button-component';
import {ShortUrl} from '../components/show-shorturl-component';
export class SmartContainer extends React.Component {
    constructor(props) {
        super(props);
        this.inputVal='';
        this.shortUrl='';
        this.state={inputValue:this.inputVal,shortUrl:this.shortUrl};
    }
    takeInput(event) {
        this.inputVal=event.target.value;
        this.setState({inputValue:this.inputVal});
        console.log(this.inputVal);
    }
    clickButton(){ 
        console.log("Button Clicked");
        Instance.post('/shorturl',{
            longUrl:this.state.inputValue
        }).then((res)=> {
            let shortUrl=res.data.doc.shortUrl;
            this.shortUrl=shortUrl;
            this.setState({...this.state,shortUrl:this.shortUrl});
        }).catch(err=> {
            console.log(err);
        })
    }
    render() {
        return(
            <React.Fragment>
                <Header/>
                <Container>
                <Input inputValue={this.takeInput.bind(this)}/>
                <SubmitButton clickBtn={this.clickButton.bind(this)}/>
                <ShortUrl shortUrlValue={this.state.shortUrl}/>
                </Container>

            </React.Fragment>
        );
    }
}